import {createStore, createEvent, createEffect, sample, merge, forward, guard} from 'effector';
import {Status, SnakePart, CellType, Direction} from './types';
import {moveSnake, populateCells, createFood, keycodeToDirection, tick} from './utils';

/** ÐŸÐµÑ€Ð²Ð¸Ñ‡Ð½Ð°Ñ Ð¸Ð½Ð¸Ñ†Ð¸Ð°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ Ð»Ð¾Ð³Ð¸ÐºÐ¸ */
const init = createEvent();

/** ÐÐ°Ð¶Ð°Ñ‚Ð¸Ðµ Ð½Ð° ÐºÐ»Ð°Ð²Ð¸ÑˆÑƒ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ */
export const keydown = createEvent<number>();

/** Ð˜Ð³Ñ€Ð¾Ð²Ð¾Ð¹ Ñ‚Ð¸Ðº Ð·Ð°Ð²Ð¸ÑÐ¸Ñ‚ Ð¾Ñ‚ Ñ‚ÐµÐºÑƒÑ‰ÐµÐ³Ð¾ ÑÑ‡ÐµÑ‚Ð° */
const fxTick = createEffect({handler: tick});

/** Ð”Ð²Ð¸Ð¶ÐµÐ½Ð¸Ðµ Ð·Ð¼ÐµÐ¸ */
const fxMove = createEffect({handler: moveSnake});

/** Ð¢ÐµÐºÑƒÑ‰Ð¸Ð¹ ÑÑ‚Ð°Ñ‚ÑƒÑ Ð¸Ð³Ñ€Ñ‹ */
export const $status = createStore<Status>(Status.START)
  .on(keydown, (status) => (status === Status.START || status === Status.END) ? Status.WAIT : Status.RUN)
  .on(fxMove.fail, () => Status.END)

/** ÐÐ°Ð¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð´Ð²Ð¸Ð¶ÐµÐ½Ð¸Ñ Ð·Ð¼ÐµÐ¸ */
const $direction = createStore<Direction>(Direction.NONE)
  .on(keydown, keycodeToDirection)
  .reset(fxMove.fail);

/** ÐšÐ»ÐµÑ‚ÐºÐ¸, Ð·Ð°Ð½ÑÑ‚Ñ‹Ðµ Ð·Ð¼ÐµÐµÐ¹ */
const $snake = createStore<SnakePart[]>([[7, 7]])
  .on(fxMove.doneData, (_, [snake]) => snake)
  .reset(fxMove.fail);

/** ÐšÐ»ÐµÑ‚ÐºÐ° Ñ ÐµÐ´Ð¾Ð¹ */
const $food = createStore<SnakePart | null>([7, 5]);

/** Ð¢ÐµÐºÑƒÑ‰Ð¸Ð¹ ÑÑ‡ÐµÑ‚ */
export const $score = createStore(0)
  .on(fxMove.doneData, (score, [_, food]) => food ? score : score + 1)
  .reset($status.updates.filter({fn: status => status === Status.WAIT}))

/** Ð˜Ð³Ñ€Ð¾Ð²Ð¾Ðµ Ð¿Ð¾Ð»Ðµ */
export const $cells = createStore<CellType[]>([]);

/** ÐŸÑ€Ð¸ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ð¸ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ð¹ Ð·Ð¼ÐµÐ¸ Ð¸Ð»Ð¸ ÐµÐ´Ñ‹ - Ð¿ÐµÑ€ÐµÑÑ‡Ð¸Ñ‚Ð°Ñ‚ÑŒ Ð¸Ð³Ñ€Ð¾Ð²Ð¾Ðµ Ð¿Ð¾Ð»Ðµ */
sample({
  source: [$snake, $food],
  clock: merge([init, $snake.updates, $food.updates]),
  target: $cells,
  fn: populateCells
});

/** ÐŸÐ¾ÑÐ»Ðµ Ð´Ð²Ð¸Ð¶ÐµÐ½Ð¸Ñ Ð·Ð¼ÐµÐ¸ ÑÐ¾Ð·Ð´Ð°Ñ‚ÑŒ Ð½Ð¾Ð²ÑƒÑŽ ÐµÐ´Ñƒ, ÐµÑÐ»Ð¸ Ð¾Ð½Ð° ÐµÑ‘ ÑÑŠÐµÐ»Ð° */
sample({
  source: $snake,
  clock: fxMove.doneData.map(([_, food]) => food),
  target: $food,
  fn: createFood,
})

/** ÐÐ° ÐºÐ°Ð¶Ð´Ñ‹Ð¹ Ñ‚Ð¸Ðº Ð¸Ð³Ñ€Ñ‹ Ð²Ð·ÑÑ‚ÑŒ Ð½ÐµÐ¾Ð±Ñ…Ð¾Ð´Ð¸Ð¼Ñ‹Ðµ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ñ Ð¸ Ð½Ð°Ñ‡Ð°Ñ‚ÑŒ Ð´Ð²Ð¸Ð¶ÐµÐ½Ð¸Ñ Ð·Ð¼ÐµÐ¸ */
sample({
  source: [$snake, $food, $direction],
  clock: fxTick,
  target: fxMove,
});

/** Ð—Ð°Ð¿ÑƒÑÐº Ñ‚Ð¸ÐºÐ° Ð¿Ñ€Ð¸ ÑÐ¼ÐµÐ½Ðµ ÑÑ‚Ð°Ñ‚ÑƒÑ Ð¸Ð»Ð¸ Ð¿Ñ€Ð¸ Ð¾ÐºÐ¾Ð½Ñ‡Ð°Ð½Ð¸Ð¸ Ð¿Ñ€ÐµÐ´Ñ‹Ð´ÑƒÑ‰ÐµÐ³Ð¾ Ñ‚Ð¸ÐºÐ° */
sample({
  source: $score,
  clock: guard(
    merge([fxTick.done, $status.updates]),
    {filter: $status.map(status => status === Status.RUN)}
  ),
  target: fxTick,
});

init();
