import {USE_PERF} from './env'

export let beginMark: (label: string) => void
export let endMark: (label: string) => void
if (USE_PERF && typeof performance !== 'undefined' && performance.mark) {
  beginMark = label => {
    performance.mark('â˜„ï¸ ' + label + ' start')
  }
  endMark = label => {
    try {
      performance.measure('â˜„ï¸ ' + label, 'â˜„ï¸ ' + label + ' start')
    } catch (err) {} // Clear marks immediately to avoid growing buffer.

    performance.clearMarks('â˜„ï¸ ' + label + ' start')
    performance.clearMeasures('â˜„ï¸ ' + label)
  }
} else {
  beginMark = label => {}
  endMark = label => {}
}
